header{
    background-color: aqua;
    align-items: center;
    display: flex;
    justify-content: space-around;
}

header div{
    display: flex;
    position: relative;
    align-items: center;
}

header input{
    height: 40px;
    width: 500px;
    border-radius: 20px;
    padding: 8px 43px 8px 8px;
    position: relative;
    font-size: 24px;
    outline: 0;
}

.a{
    font-size: 28px;
    position: absolute;
    right: 8px;
    text-decoration: none;
    color: black;
    cursor: pointer;
}