.blockBackground{
    margin:16px;
    display: flex;
    flex-direction: column;
    align-items: center;


    padding: 8px;
    width: 290px;
    height: 400px;
    border: black 1.4px solid;
    cursor: pointer;
    transition: 0.2s;
}

.blockBackground:hover{
    opacity: 0.9;
    transform: scale(1.03);
}

.blockTop{
    display: flex;
    width: 100%;
    height: 100%;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: 50%;
    padding: 8px;
    box-sizing: border-box;
    align-items: center;
    justify-content: center;
}

.blockBottom{
    display: flex;
    flex-direction: column;
    align-items: center;


    padding: 8px;
}

.blockBottom h1{
    display: flex;
    margin: 4px;
    word-break: break-all;

    font-size: 14px;
}

.blockBackground p{
    display: flex;
    margin: 2px;
    word-break:keep-all;
    text-align: center;
}

